.card {
    height: 14rem;
    width: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 11rem;
    text-align: center;
    background: rgba(176, 176, 166, 0.26);
    border: 7px solid rgb(245, 163, 32);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
}

.card>img {
    transform: scale(0.6);
    margin-bottom: -2rem;
}

.card span:nth-child(3) {
    color: rgb(87, 83, 83);
    font-size: 16px;
    text-wrap-mode: nowrap;
}

.c-button {
    width: 8rem;
    background: rgb(244, 234, 234);
    border-radius: 20px;
    border: none;
    padding: 6px;
    font-size: 20px;
    color: rgb(103, 4, 241);
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.8);
    cursor: pointer;
}