/* Default styles (for larger screens like desktop) */
.floatingDiv {
  justify-content: space-around;
  display: flex;
  background: rgb(255, 255, 255);
  box-shadow: var(--boxShadow); /* Make sure to define --boxShadow variable in your root */
  border-radius: 15px;
  align-items: center;
  padding: 2px 20px 2px 2px;
  height: 5rem;
  width: 12rem; /* Set a default width */
}

.floatingDiv > img {
  transform: scale(0.5);
}

.floatingDiv > span {
  font-family: sans-serif;
  font-size: 16px;
}

/* Positioning for floatingdiv1 and floatingdiv2 (Desktop) */
.floatingdiv1 {
  margin-left: 20rem;
  margin-top: -2rem;
}

.floatingdiv2 {
  margin-left: -4.3rem;
  margin-top: 21.3rem;
}

/* Media Queries for mobile responsiveness */
@media only screen and (max-width: 768px) {
  /* Stack the floating div content vertically */
  .floatingDiv {
    height: auto;
    width: 10rem; /* Slightly reduce width on tablet */
    padding: 10px;
  }

  .floatingDiv > img {
    transform: scale(0.4); /* Scale down image for smaller screens */
  }

  .floatingDiv > span {
    font-size: 14px; /* Reduce font size for better readability */
  }

  /* Adjust the positioning of floating divs for smaller screens */
  .floatingdiv1 {
    margin-left: 10rem;
    margin-top: -1rem;
  }

  .floatingdiv2 {
    margin-left: -3rem;
    margin-top: 16rem;
  }
}

@media only screen and (max-width: 480px) {
  /* Make further adjustments for very small screens (Mobile phones) */
  .floatingDiv {
   display: none;
  }

  .floatingDiv > img {
    display: none;
  }

  .floatingDiv > span {
    display: none; /* Further reduce font size */
  }

  /* Adjust the positioning of floating divs */
}
