/* Default Styles (Desktop or Larger Screens) */
.intro {
    display: flex;
    margin-top: 10rem;
    padding: 0 3rem 0 3rem;
    height: 77vh;
}

.i-left {
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}

.i-name {
    display: flex;
    flex-direction: column;
}

.i-name>:nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 4rem;
}

.i-name>:nth-child(2) {
    color: rgb(255, 0, 0);
    font-weight: bold;
    font-size: 3.5rem;
}

.i-name>:nth-child(3) {
    font-weight: 100;
    font-size: 20px;
    color: var(--gray);
}

.i-button {
    top: 4rem;
    height: 2rem;
    width: 6rem;
}

.i-icons {
    display: flex;
    margin-top: 8rem;
    gap: 3rem;
}

.i-icons>* {
    transform: scale(.7);
}

.i-icons>* {
    cursor: pointer;
}

.i-right {
    flex: 1;
    position: relative;
    margin-top: 4rem;
    margin-left: 10rem;
}

.i-right>* {
    position: absolute;
    z-index: 1;
}

.i-right>:nth-child(1) {
    transform: scale(.80);
    left: -20%;
    top: -8%;
}

.i-right>:nth-child(2) {
    transform: scale(.82);
    left: -6.5rem;
    top: -3rem;
}

.i-right>:nth-child(3) {
    left: -3rem;
    top: -0.5rem;
    transform: scale(1);
}

.i-right>:nth-child(4) {
    transform: scale(0.7);
    top: -8rem;
    left: -13rem;
    padding: 0;
    border-radius: 50px;
}

.blur {
    position: absolute;
    width: 22rem;
    height: 15rem;
    border-radius: 190px;
    background: black;
    z-index: -9;
    top: -18%;
    left: 56%;
    filter: blur(50px);
}

.blur:nth-child(2) {
    background: rgb(238 210 255);
    left: -14rem;
    top: 17rem;
    width: 21rem;
    height: 15rem;
}

/* Mobile Responsiveness */
@media only screen and (max-width: 768px) {

    /* Adjust the intro section */
    .intro {
        flex-direction: column;
        height: auto;
        padding: 2rem;
        margin-top: 3rem;
    }

    /* Adjust the left section */
    .i-left {
        flex: 1;
        align-items: center;
        text-align: center;
    }

    .i-name {
        margin-bottom: 1rem;
    }

    .i-name>:nth-child(1) {
        font-size: 2.5rem;
    }

    .i-name>:nth-child(2) {
        font-size: 2rem;
    }

    .i-name>:nth-child(3) {
        font-size: 16px;
    }

    /* Adjust the "Hire me" button */
    .i-button {
        height: 2.5rem;
        width: 8rem;
        font-size: 14px;
    }

    /* Adjust the icons section */
    .i-icons {
        display: flex;
        justify-content: center;
        margin-top: 3rem;
        gap: 1.5rem;
    }

    .i-icons>* {
        transform: scale(1);
    }

    /* Adjust right section */
    .i-right {
        position: relative;
        margin-top: 4rem;
        margin-left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .i-right>:nth-child(1),
    .i-right>:nth-child(2),
    
    .i-right>:nth-child(4) {
        display: none;
    }
    
    .i-right>:nth-child(3){
        position: relative;
        transform: scale(0.8);
        left: 0;
        top: 0;
    }

    .i-right>:nth-child(3) {
        transform: scale(1);
    }

    /* Adjust the blurred divs */
    .blur {
        display: none;
    }

    .blur:nth-child(2) {
       display: none;
    }
}

@media only screen and (max-width: 480px) {

    /* Adjust text sizes and layout further for very small screens */
    .i-name>:nth-child(1) {
        display: none;
    }

    .i-name>:nth-child(2) {
        display: none;
    }

    .i-name>:nth-child(3) {
        font-size: 14px;
    }

    .i-button {
        height: 3rem;
        width: 9rem;
        font-size: 16px;
    }

    /* Center the icons and make them bigger */
    .i-icons {
        display: none;
    }

    .i-icons>* {
       display: none;
    }

    /* Adjust the blurred divs */
    .blur {
       display: none;
    }

    .blur:nth-child(2) {
        background: rgb(238 210 255);
        left: 50%;
        top: 17rem;
        width: 16rem;
        height: 10rem;
    }
}
