
.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
    padding: 0 3rem 0 3rem;
    
}

.n-left{
    flex:1;
    align-items: center;
    display: flex;
    gap: 2rem;
}

.n-name{
    font-size: 1.3rem;
    font-weight: bold; 

}

.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    flex: 10
;
}

.n-list > ul{
    display: flex;
    gap: 3rem;
    margin: 4rem;

}



